import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Home from '@/page/home/home'
import  Plist from '@/page/Plist/plist'
import Search from '@/page/search/search'
import SingerPage from '@/page/singerPage/singer'
import SonPage from '@/page/singerPage/sonPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/home',
      component: Home
    },
    {
      path:'/',
      redirect: '/home'
    },
    {
      path: '/plist',
      component: Plist
    },
    {
      path: '/search',
      component: Search
    },
    {
      path: '/singerPage',
      component: SingerPage,
      children: [ // children表示子 下面可以有多个{}
        {
          path: ':id', // :id表示计数变量
          component: SonPage
        }
      ]
    }
  ]
})



