
<template>
  <section>
    <top-bar></top-bar>
    <roll></roll>
    <p class="dt">电台</p>
    <load>
      <div v-for="(m,index) in loadata" class="slotBox">
        <a :href="index == 0 ? slotBoxAurl[0] : slotBoxAurl[1]">
          <img :src="m.picUrl">
          <h5>{{m.Ftitle}}</h5>
        </a>
      </div>
    </load>
    <div class="hotSinger">
      <ul>
        <li v-for="(m,index) in hotData">
          <a :href="hotListData[index]">
            <div>
              <img :src="m.picUrl">
              <span></span>
              <span></span>
            </div>
            <div class="text">
              <h3>{{m.songListDesc}}</h3>
              <p>{{m.songListAuthor}}</p>
            </div>
          </a>
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
  import TopBar from '@/components/topBar'
  import Roll from '@/base/pintRoll/roll'
  import Load from '@/base/pintRoll/load'
  import {GetHome} from "../../api/home"

  export default {
    data() {
      return {
        pint: null,
        loadata: null,
        hotData: null,
        slotBoxAurl: ['https://i.y.qq.com/v8/playsong.html?ADTAG=myqq&from=myqq&channel=10007100&songid=213471351,216088608,217911315,214092055,217966946,217072936,218224540,212877900,218224536,213878341,213918583,108991193,217935958,216005563,217509213,214203331,215298127,217600534,219721429,214713358',
        'https://i.y.qq.com/v8/playsong.html?ADTAG=myqq&from=myqq&channel=10007100&songid=106484214,102679374,231725,1799991,103848,109188911,103546,102300586,639141,311553,95638,105738437,99247,5601957,5162114,1930969,4744062,184153,803516,144184'],
        hotListData: ['https://y.qq.com/w/taoge.html?ADTAG=myqq&from=myqq&channel=10007100&id=2646688496',
        'https://y.qq.com/w/taoge.html?ADTAG=myqq&from=myqq&channel=10007100&id=1144416825',
        'https://y.qq.com/w/taoge.html?ADTAG=myqq&from=myqq&channel=10007100&id=2043041547',
        'https://y.qq.com/w/taoge.html?ADTAG=myqq&from=myqq&channel=10007100&id=2040362185',
        'https://y.qq.com/w/taoge.html?ADTAG=myqq&from=myqq&channel=10007100&id=1723063372',
        'https://y.qq.com/w/taoge.html?ADTAG=myqq&from=myqq&channel=10007100&id=3482605622']
      }
    },
    components: {
      TopBar,
      Roll,
      Load
    },
    mounted() {
      this.getdata()
    },
    methods: {
      getdata() {
        GetHome().then(pos => {
          this.loadata = pos.data.radioList
          this.hotData = pos.data.songList
          console.log(this.loadata)
        }).catch(err => {
          console.log(err)
        })
      }
    }
  }
</script>

<style lang="less" scoped>

  section {
    padding-bottom: 1px;
    .slotBox {
      width: 10rem;
      height: 100%;
    }
    img {
      width: 100%;
      height: 100%;
    }
    h5 {
      margin-top: 0.2rem;
    }
  }
  .dt {
    color: #DCB310;
    margin-top: 0.5rem;
  }
  .hotSinger{
    width:100%;
    height:20rem;
    margin-top: 3rem;
    ul{
      margin-left: -1rem;
      li{
        margin-top: 0.5rem;
        display: inline-block;
        margin-left: 1rem;
        div{
          width:10rem;
          height:10rem;
          img{
            width:10rem;
            height:10rem;
          }
        }
        .text{
          margin: 1rem 0;
          width:10rem;
          height:1rem;
          h3{
            color: #DCB310;
            max-width:100%;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;
          }
          p{
            padding: 0.4rem 0;
            color: #DCB310;
            font-size: 0.5rem;
          }
        }
      }
    }
  }

</style>



