<template>
  <header class="header">
    <div class="top-text">
      <div class="top-item">
        <img src="">
        <h3>LXG Music Play</h3>
      </div>
      <span class="top-svg">
        <svg></svg>
      </span>
    </div>
    <div class="top-tabLine">
      <span class="recommend" @click="skip('/home')" :class="{textBj:$route.path.indexOf('/home') != -1 }">推荐</span>
      <span class="singer" @click="skip('/singerPage')" :class="{textBj:$route.path.indexOf('/singerPage')!=-1}">歌手</span>
      <span class="Plist" @click="skip('/plist')" :class="{textBj:$route.path.indexOf('/plist')!=-1}">排行</span>
      <span class="search" @click="skip('/search')" :class="{textBj:$route.path.indexOf('/search')!=-1}">搜索</span>
    </div>
  </header>
</template>

<script>
  export default {
    methods: {
      skip(path) {
        this.$router.push(path)
      }
    }
  }
</script>

<style lang="less" scoped>
  .header {
    background: #212121;
    width: 100%;
    height: 6rem;
    position: fixed;
    top: 0;
    left: 0;
    color:#868686;
    .top-text {
      width: 100%;
      height: 3.5rem;
      /*color: #E1B814;*/
      display: flex;
      .top-item {
        h3{
          color: #F6C70E;
        }
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .top-svg {
        width: 3rem;
        height: 100%;
      }
    }
    .top-tabLine {
      display: flex;
      width: 100%;
      height: 2rem;
      justify-content: space-around;
      align-items: center;
      span {
        display: block;
        height: 100%;
        line-height: 2rem;
      }
      .textBj{
        border-bottom: 0.1rem solid saddlebrown;
        color: #F6C70E;
      }
    }
    .top-svg{
      color: pink;
      svg{
        width:2rem;
        height:2rem;background: #acbdef;
        border-radius: 50%;
        margin-top: 0.2rem;
      }
    }
  }

</style>



