<template>
  <section>
    <top-bar></top-bar>
    <ul class="pintUl">
      <li v-for="(m,index) in singerRoll" :data-index="index" >
        <span class="topText">{{m.title}}</span>
        <div v-for="(v,index) in m.items" @click="sonSinger(Math.random(), m)">
          <img :src="v.headPint">
          <span class="pintText">{{v.name}}</span>
        </div>
      </li>
    </ul>
    <ul class="rightTextUl">
      <li v-for="(n,index) in rightListText" @click="sliderPosition(index)">
        {{n}}
      </li>
    </ul>
    <router-view></router-view>
  </section>
</template>

<script>
  import TopBar from '@/components/topBar'
  import {getSingerList} from "../../api/singer"
  import {mapMutations} from 'vuex'

  let HOT_SINGER_LEN = 10
  let HOT_TITLE = '热门'
  export default {
    data() {
      return {
        singers: null,
        singerRoll: null,
        rightListText: null
      }
    },
    components: {
      TopBar
    },
    mounted() {
      this._getSingerList()
      this.listRoll()
      // this._hotSingers()
    },

    methods: {
      sonSinger(val, m) {
        this.$router.push({
          path: `/singer/${val}`
        })
        this.mapMutations(m)
        // this.$router.push(val)
      },
      listRoll() {
        let pintUl = document.querySelector('.pintUl')
        pintUl.addEventListener('scroll', function () {
          console.log(1)
        })
      },
      sliderPosition(val) {
        console.log(val)
      },
      _getRightText(data) {
        return data.map((item, index) => {
          return item.title.substr(0, 1)
        })
      },
      _getSingerList() {
        getSingerList().then(pos => {
          this.singers = pos.data.list
          this.singerRoll = this._hotSingers(pos.data.list)
          this.rightListText = this._getRightText(this.singerRoll)
        }).then(() => {
          let pintUl = document.querySelector('.pintUl')
          let pintUlLi = document.querySelectorAll('.pintUl li')
          let arr = []
          for (let i = 0; i < pintUlLi.length; i++) {
            arr.push(pintUlLi[i].offsetTop)
          }
          // scroll
          console.log(arr)
          let i = 112
          window.addEventListener('scroll', function (e) {
            // console.log(pintUlLi[0].clientHeight, pintUlLi[1].offsetTop) 112
            window.addEventListener('mousewheel', function (e) {
              // e.wheelDelta为-120就是往下滑 +120就是往上滑
              if (e.wheelDelta < 0) {
                i++
              } else if (e.wheelDelta > 0) {
                i--
              }
              // console.log(i)
            })
          })
        }).catch(err => {
          console.log(err)
        })
      },
      _hotSingers(list) {
        let map = {
          hot: {
            title: HOT_TITLE,
            items: []
          }
        }
        list.forEach((item, index) => {
          if (index < HOT_SINGER_LEN) {
            map.hot.items.push({
              id: item.Fsinger_mid,
              name: item.Fsinger_name,
              headPint: `http://y.gtimg.cn/music/photo_new/T001R150x150M000${item.Fsinger_mid}.jpg?max_age=2592000`
            })
          }
          const key = item.Findex
          if (!map[key]) {
            map[key] = {
              title: key,
              items: []
            }
          }
          map[key].items.push({
            id: item.Fsinger_mid,
            name: item.Fother_name,
            headPint: `http://y.gtimg.cn/music/photo_new/T001R150x150M000${item.Fsinger_mid}.jpg?max_age=2592000`
          })
        })
        let hot = []
        let lists = []
        for (let k in map) {
          // match必须   因为有一个元素的title没有所以报错
          if (map[k].title.match(/[a-zA-Z]/)) {
            lists.push(map[k])
          } else if (map[k].title == HOT_TITLE) {
            hot.push(map[k])
          }
        }
        lists.sort((a, b) => {
          // charCodeAt(index)  表示将字符串的index位置的缩引  返回一个Uncode编码进行数字上的升排序
          return a.title.charCodeAt(0) - b.title.charCodeAt(0)
        })
        // console.log(hot.concat(lists))
        return hot.concat(lists)
      },
      ...mapMutations({
        setSinger: 'GET_SINGER'
      }),
    }
  }
</script>

<style lang="less" scoped>
  .pintUl {
    margin-top: 7rem;
    width: 100%;
    li {
      .topText {
        width: 100%;
        height: 2rem;
        line-height: 2rem;
        background: #464546;
        color: rgba(255, 255, 255, 0.79);
        box-sizing: border-box;
        padding-left: 2rem;
        text-align: left;
      }
      width: 100%;
      display: flex;
      flex-direction: column;
      div {
        width: 100%;
        display: flex;
        justify-content: left;
        align-items: center;
        img {
          width: 5rem;
          height: 5rem;
          border-radius: 50%;
          margin: 2rem 2rem 1.5rem;
        }
      }
    }
  }

  .rightTextUl {
    width: 2rem;
    position: fixed;
    right: 0;
    bottom: 50%;
    transform: translateY(60%);
    background: #464546;
    color: rgba(244, 244, 244, 0.73);
    border-radius: 1rem;
    li {
      margin: 0.3rem 0;
    }
  }
</style>



