<template>
  <section class="pintRoll" ref="slider">
    <swiper :options="swiperOption">
      <swiper-slide v-for="m in sliderData">
        <a :href="m.linkUrl"><img :src="m.picUrl"></a>
      </swiper-slide>
    </swiper>
  </section>
</template>

<script>
  import {GetHome} from "../../api/home"
  import {ERR_OK} from "../../api/config"

  export default {
    data() {
      return {
        swiperOption: {
          // loop: true,
          autoplay: {
            delay: 3000,
            disableOnInteraction: false,
            // autoplayDisableOnInteraction : false
          },
          pagination: {
            el: '.swiper-pagination',
            // clickable :true,
          },
        },
        sliderData: null
      }
    },
    mounted() {
      this._getImage()
    },
    methods: {
      _getImage() {
        GetHome().then(pos => {
          if(pos.code == ERR_OK){
            this.sliderData = pos.data.slider
            console.log(pos)
          }else {
            console.log('请求错误')
          }
        }).catch(err => {
          console.log('api请求失败')
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .pintRoll {
    z-index: 1;
    width: 100%;
    height: 10rem;
    margin-top: 6.5rem;
    .swiper-pagination{
      width:100%;
      height:1rem;
      margin-top: 8rem;
    }
    div {
      width: 100%;
      height: 100%;
      img{
        width:100%;
        height:100%;
      }
    }
  }
</style>



